.site-navbar{
  padding: 18px 0;
}
.main-menu{
  @include desktops{
    display: flex;
    justify-content: flex-end;
  }
  >li{
    >.nav-link{
      @include desktops{
        color: #353638!important;
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
        padding-top: 18px!important;
        padding-bottom: 18px!important;
        padding-left: 25px!important;
        padding-right: 25px!important;

      }
      &:hover{
        color: $primary-color-p5;
      }
    }
  }
  .nav-item.dropdown{
   @include desktops{
    position: relative;
    z-index: 99;
   }
   &:hover{
    .menu-dropdown{
      @include desktops{
        top: 100%;
        opacity: 1;
        pointer-events: visible;
      }
      p{
        margin-bottom: 0;
      }
    }
   }
  }
  .menu-dropdown{
    @include desktops{
      display: flex;
      top: 110%;
      position: absolute;
      flex-wrap: wrap;
      min-width: 530px;
      box-shadow: 0 12px 24px rgba(0, 0, 0, 0.03);
      border-radius: 8px;
      border: 1px solid #e5e5e5;
      background-color: #ffffff;
      padding: 30px 30px 0px;
      z-index: 99;
      opacity: 0;
      transition: opacity .4s,top .4s;
      pointer-events: none;
      left: -90%;
    }
    .single-item{
      @include desktops{
        flex: calc(50% - 20px);
        max-width: calc(50% - 20px);
        margin-bottom: 20px;
      }
      &:hover{
        a{
         h3{
           
          @include desktops{
            color: $primary-color-p5;
           }
         }
        }
      }
      a{
          h3{
            color: #1f1f1f;
            font-size: 16px;
            font-weight: 700;
            letter-spacing: -0.4px;
            transition: .4s;
            padding: 0;
            padding-bottom: 5px;
            line-height: 1;
          }
          p{
            margin-bottom: 0;
          }
      }
      p{
        color: #878b90;
        font-size: 14px;
        font-weight: 300;
        letter-spacing: -0.35px;
      }
      &:nth-child(odd){
           @include desktops{
              margin-right: 40px;
           }
      }
    }
  }
}

.offcanvas-active{
  
  &.navbar-expand-lg{
    .btn-close-off-canvas{
      @include desktops{
        display: none;
      }
    }
    @include till-desktop{
      .navbar-collapse{
        display: block;
        position: fixed;
        top: 0;
        background: #fff;
        left: -100%;
        width: 250px;
        padding-left: 20px;
        padding-right: 20px;
        height: 100%;
        transition:left .4s;
        z-index: 9999;
        box-shadow: 0 0 87px 0 rgba(0, 0, 0, 0.09);
        padding-top: 50px;
        @include mobile {
          width: 300px;
        }
        &.show{
          left: 0%;
          ~.btn-close-off-canvas{
            .icon-burger{
              display: none;
            }
            .icon-close{
              display: block;
            }
          }
        }
        &.collapsing{
          transition:height 0s;
          height: 100%;
        }
      }
      .btn-close-off-canvas{
        .icon-burger{
          display: block;
        }
        .icon-close{
          display: none;
        }
      }
      .main-menu {
        >li {
            padding-bottom: 13px;
            margin-bottom: 13px;
            border-bottom: 1px solid #ececec;
            >.nav-link{
              padding: 0;
            }
            .menu-dropdown {
              border: 0;
              border-radius: 0;
              min-width: auto;
              padding: 0;
                >li {
                    padding-top: 13px;
                    margin-top: 13px;
                    border-top: 1px solid #ececec;
                    padding-left: 20px;
                    &.single-item{
                      h3 {
                          font-size: 15px;
                          margin-bottom: 5px;
                          font-weight: 600;
                          line-height: 1;
                      }
                      p{
                          font-size: 13px;
                      }
                      a{
                          &:hover{
                              color: inherit;
                          }
                      }
                   }
                }
            }
            &:last-child {
              border-bottom-color: transparent;
            }
        }
        li {
          i {
            margin-left: 8px;
            position: relative;
            top: 3px;
          }
          &:hover {
              >a {
                  color: $primary-color-p5;
              }
          }
        }
        a {
            display: block;
        }
      }

    }

    // .btn-close-off-canvas{
    //   display: ;
    // }
  }
  
}