header.branding-center {
    .site-nav{
      justify-content: space-between;
      padding-top: 28px;
      padding-bottom: 20px;
      .navbar-brand{
        @include desktops{
          order: 2;
          flex: 20%;
          text-align: center;
        }
      }
      .navbar-nav{
        order: 2;
        @include desktops{
          order: 1;
          flex: 60%;
        }
        .nav-item{
          .nav-link {
            color: $text-dark;
            font-size: 16px;
            font-weight: 300;
            margin-right: 30px;
          }
        }
      }
      .header-link {
        @include desktops{
          order: 3;
          flex: 20%;
          text-align: right;
          color: $text-dark;
          font-size: 16px;
          font-weight: 300;
        }
      }
    }
  }
  .job-listing {
    &.hero-section {
      .heroe-bg-gradient {
        position: relative;
        z-index: 1;
        &:before{
          position: absolute;
          content: "";
          left: 0%;
          height: 100%;
          width: 100%;
          top: 0;
          background-image: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, rgba(18, 27, 33, 0.76) 100%);
          z-index: -1;
        }
      }
      .hero-bg-img {
        background: url(../image/hero-bg.png) no-repeat center;
        background-size: cover;
      }
      
      .hero-content {
        padding-bottom: 100px;
        padding-top: 100px;
        .title, p {
          color: #fff;
        }
        .indicator i {
          font-size: 54px;
          color: #fff;
        }
      }
  
    }
  }
  
  
  .input-with-icon.icon-left {
    // width: 100%;
    flex: 100%;
    max-width: 100%;
    height: 70px;
    position: relative;
    margin-bottom: 16px;
    .input-icon {
        position: absolute;
        top: 50%;
        left: 35px;
        transform: translateY(-45%);
        color: $text-light;
        font-size: 18px;
    }
    input {
      height: 100%;
      padding-left: 76px;
      background-color: #ffffff;
      border-color: transparent;
      color: $text-light;
      font-size: 16px;
      font-weight: 300;
    }
    @include desktops {
      // height: 107px;
    }
  }
  
  .search-form {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 0px;
    box-shadow: 0 32px 84px rgba(0, 0, 0, 0.22);
    .form-group{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      flex: 100%;
      max-width: 100%;
      margin-bottom: 0;
      .nice-select{
        flex: 100%;
        max-width: 100%;
        height: 70px;
        border-radius: 0;
        border: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 16px;
        .current{
          color: #1d293f;
          font-size: 16px;
          font-weight: 300;
          position: relative;
          display: flex;
          align-items: center;
          &:before{
            content: "\ea35";
            font-family: 'ep';
            font-size: 18px;
            color: #8c97ac;
            margin-right: 20px;
          }
        }
        &::after {
          right: 40px;
          height: 11px;
          width: 11px;
          margin-top: -8px;
        }
      }
    }
    @include desktops {
    background-color: white;
      .form-group {
        flex: calc(100% - 288px);
        .input-with-icon {
          flex: 60%;
          max-width: 60%;
          margin-bottom: 0;
        }
        .nice-select {
          flex: 40%;
          max-width: 40%;
          border-left: 3px dotted #e2e4e8;
          margin-bottom: 0;
  
        }
  
      }
    }
  
    .btn--primary {
      width: 100%;
      font-size: 16px;
      font-weight: 500;
      height: 70px;
      margin-bottom: 30px;
      @include desktops {
        margin-bottom: 0;
        height: 107px;
        width: 280px;
      }
      @include large-desktops {
        height: 107px;
        width: 288px;
  
      }
    }
    .list {
      width: 100%;
    }
  }
  
  .brand-section {
    text-align: center;
  }
  .brands-wrapper {
    flex-wrap: wrap;
  }
  

  
  .explore-slider {
    .slick-list {
      padding-top: 75px!important;
      padding-bottom: 150px!important;
      margin-right: -15px;
      margin-left: -15px;
    }
    .single-slide{
      margin-right: 15px;
      margin-left: 15px;
    }
   
   
  }
  .explore-slide-nav{
    .slick-arrow {
      width: 53px;
      height: 53px;
      border-radius: 50%;
      box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
      background-color: $sky-blue;
      color: #fff;
      i {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &.slick-prev {
        border: 1px solid rgba(34, 176, 252, 0.25);
        background-color: $background-light;
        color: $sky-blue;
        margin-right: 12px;
      }
    
    }
  }
  
 
  
  .job-gallery-section {
    background-color: #f5fafd;
    .btn--primary {
      margin: 0 auto;
      margin-top: 20px;
      @include tablet {
        // margin-left: auto;
        margin-right: 0;
        margin-top: 0px;
      }
    }
  }
  
  // Job-listing Newsletter Section 
  
  .job-listing-section {
    background: #fff;
    padding-top: 100px;
    padding-bottom: 100px;
    @include desktops {
      padding-bottom: 150px;
    }
  }
  .newsletter {
    .sub-title {
      color: #f77321;
      font-size: 16px;
      font-weight: 300;
    }
    @include desktops {
      padding-left: 14%;
    }
  
  }
  
  .left-content-wrapper {
    position: relative;
    z-index: 1;
    .small-img {
        position: absolute;
        top: -30px;
        left: -42px;
        z-index: -1;
    }
    .alert-img {
      position: absolute;
      right: -17%;
      top: 38%;
    }
  }
  .newsletter-form {
    .input-with-icon.icon-left {
      width: 100%;
      height: 70px;
      margin-bottom: 16px;
      position: relative;
      .input-icon {
          position: absolute;
          top: 50%;
          left: 35px;
          transform: translateY(-45%);
          color: $text-light;
          font-size: 20px;
      }
      input {
        height: 100%;
        padding-left: 76px;
        box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
        background-color: #ffffff;
        border-color: transparent;
        color: $text-light;
        font-size: 16px;
        font-weight: 300;
      }
    }
  
    .btn--primary {
      width: 100%;
      height: 70px;
    }
    @include desktops {
      .input-with-icon.icon-left {
        flex-basis: calc(100% - 160px);
        height: 107px;
        margin-bottom: 0px;
      }
      .btn--primary {
        height: 107px;
        width: 160px;
      }
    }
  }
  
  
  
  // Latest News Blog styling

  .blog-card {
    padding: 36px;
    box-shadow: 0 2px 4px rgba(14, 86, 124, 0.17);
    border-radius: 2px;
    background-color: #ffffff;
    transition: .4s;
    .card-img {
      margin-bottom: 25px;
    }
    .excerpt-details {
      color: #8c97ac;
      font-size: 13px;
      font-weight: 300;
    }
    @include desktops {
      display: flex;
      align-items: center;
      .card-img {
        border-radius: 0;
        margin-right: 36px;
        max-width: 116px;
        min-width: 116px;
        margin-bottom: 0;
      }
      .card-text {
        flex: calc(100% - 155px);
      }
  
    }
    &:hover {
      box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
    }
  }
  
  
  // Footer styling

  
  .single-footer-wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .single-footer {
      margin-right: 70px;
      &:last-child {
        margin-right: 0;
      }
    }
    
  }
  .footer-title {
    margin-bottom: 32px;
    color: $text-dark;
  }
  .footer-text {
    color: #7c8087;
  }
  .footer-list {
    color: #7c8087;
    li {
      color: inherit;
      font-size: 16px;
      font-weight: 300;
      line-height: 22px;
      margin-bottom: 17px;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  
  .copyright-bar {
    padding-top: 70px;
    color: #7c8087;
    font-size: 13px;
    font-weight: 300;
    line-height: 28px;
    text-align: center;
    @include desktops {
      display: flex;
      justify-content: space-between;
      padding-top: 120px;
      text-align: left;
    }
    p{
      font-size: 13px;
      font-weight: 300;
      line-height: 28px;
    }
    .copyright {
      font-size: 13px;
      font-weight: 300;
      line-height: 28px;
    }
  }
  
  
  
  
  // Plugin page
  
  
  header.branding-left {
    .site-nav{
      justify-content: space-between;
      padding-top: 28px;
      padding-bottom: 20px;
      .navbar-brand{
        @include desktops{
          flex: 20%;
        }
      }
      .navbar-nav{
        @include desktops{
          flex: 60%;
        }
        .nav-item{
          .nav-link {
            color: $text-dark;
            font-size: 16px;
            font-weight: 300;
            margin-right: 30px;
          }
        }
      }
      .header-link {
        @include desktops{
          flex: 20%;
          text-align: right;
          color: $text-dark;
          font-size: 16px;
          font-weight: 300;
        }
      }
    }
  }
  
  .plugin {
    .hero-img {
      position: relative;
      .shape-1 {
        position: absolute;
        top: 26px;
        left: 180px;
        z-index: 1;
      }
      
      .big-img-1 {
        position: absolute;
        top: 0;
        left: -20px;
        z-index: 2;
      }
      
      .shape-2 {
        position: absolute;
        top: 250px;
        left: 80px;
        z-index: 3;
      }
      
      .notice-img {
        position: absolute;
        top: 170px;
        left: 100px;
        z-index: 4;
      }
      
      .big-img-2 {
        position: absolute;
        top: 97px;
        right: -50px;
        z-index: 3;
      }
    }
    
  }
  .feature-img {
    position: relative;
    .pattern {
      position: absolute;
      top: -32px;
      left: -24px;
      z-index: -1;
    }
    .notice-1 {
        position: absolute;
        top: 100px;
        right: -175px;
    }
    .notice-2 {
        position: absolute;
        bottom: 10px;
        left: -115px;
    }
  }
  
  .content-sec1 {
    padding-top: 100px;
    border-top: 1px solid #e2e4e8;
    background-image: linear-gradient(to top, rgba(246, 252, 255, 0) 0%, #f7f7f7 100%);
    @include desktops {
      padding-top: 195px;
    }
  }
  
  .content1-img-right {
    position: relative;
    img {
      width: 100%;
    }
    .eclipse-shape {
      position: absolute;
      right: 0;
      top: 0px;
      opacity: .7;
      filter: contrast(1.9);
    }
    
    .pattern {
      position: absolute;
      top: -40px;
      left: -72px;
    }
  }
  .content2-img-left {
    position: relative;
    .eclipse-shape {
      position: absolute;
      left: 0;
      top: 0px;
      opacity: .7;
      filter: contrast(1.9);
    }
    
    .pattern {
      position: absolute;
      bottom: -36px;
      left: -35px;
      z-index: -1;
    }
  }
  
  

  .testimonial-section{
      padding-top: 65px;
      padding-bottom: 100px;
      @include tablet{
        padding-bottom: 170px;
      }
  }
  
  .testimonial-container{
    @include tablet{
      height: 320px;
      position: relative;
    }
  }
  
  
  .testimonial-head {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 40px;
    @include desktops{
        margin-bottom: 65px;
    }
    .icon-img{
        margin-bottom: 40px;
    }
}
  
.testimonial-wrapper{
    @include tablet{
      position:absolute;
      top: 0;
      left: 15px;
      width: 160%;
    }
    @include desktops{
      width: 120%;
    }
    @include brk-point(1200px){
      width: 130%;
    }
    @include brk-point(1360px){
      width: 140%;
    }
    
  .testimonial-slider{
    @include tablet{
      .single-slide{
        margin-right: 30px;
      }
    }
    .slick-dots{
        display: flex;
        margin-top: 56px;
        li{
            height: 5px;
            width: 30px;
            margin-right:15px;
            background: #e2e4e8;
            font-size: 0;
            border-radius: 6px;
            position: relative;
            overflow: hidden;
            cursor: pointer;
            &:before{
                content: '';
                position: absolute;
                top:0;
                left: 0;
                background:#258aff;
                width: 0;
                height: 100%;
                transition: .4s;
            }
            &.slick-active{
                &:before{
                    width: 100%;
                }
            }
        }
    }
  }
  }
.testimonial-slider-nav {
    margin: 0 -7.5px 0;
    margin-top: 15px;
    @include desktops{
        margin-top: 0px;
        margin-bottom: 15px;
    }
    .slick-arrow {
        font-size: 24px;
        align-items: center;
        display: inline-flex;
        color: #e2e4e8;
        padding: 0 7.5px;
        transition: .4s;
        &:hover{
            color:#258aff;
        }
    }
}


  
