.btn{
    min-width: 193px;
    height: 65px;
    border-radius: 0;
    font-size: 16px;
    font-weight: 500;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    &-p3{
        min-width: 193px;
        height: 65px;
        font-size: 16px;
        font-weight: 500;
        border-radius: 5px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        padding-left: 15px;
        padding-right: 15px;
    }
    &-p4{
        min-width: 150px;
        height: 45px;
        font-size: 16px;
        font-weight: 500;
        border-radius: 5px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        padding-left: 15px;
        padding-right: 15px;
    }
    &-p5{
        min-width: 150px;
        height: 45px;
        font-size: 16px;
        font-weight: 500;
        border-radius: 5px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        padding-left: 30px;
        padding-right: 30px;
    }
    &--primary {
        background: $primary-color;
        color: #fff;
        &:hover {
            background: rgba($primary-color, 0.7);
            color: #fff;
        }
        &-3 {
            background: $primary-color-p3;
            color: #fff;
            &:hover {
                background: rgba($primary-color-p3, 0.7);
                color: #fff;
            }
        }
        &-4 {
            background: $primary-color-p4;
            color: #fff!important;
            &:hover {
                background: rgba($primary-color-p4, 0.7);
                color: #fff!important;
            }
        }
        &-5 {
            background: $primary-color-p5;
            color: #fff!important;
            &:hover {
                background: rgba($primary-color-p5, 0.7);
                color: #fff!important;
            }
        }
    }
    &--secondary{
        &-4 {
            background: $secondary-color-p4;
            color: #fff;
            &:hover {
                background: rgba($secondary-color-p4, 0.7);
                color: #fff;
            }
        }
    }
    &--outlined{
        &-p3{
            border: 2px solid #e2e4e8;
            color: #1d293f;
            background: #fff;
            &:hover {
                background: $primary-color-p3;
                color: #fff;
                border-color: $primary-color-p3;
            }
        }
        &-p4{
            border: 2px solid #e2e4e8;
            color: #1d293f;
            background: transparent;
            color: #fff;
            &:hover {
                background: $secondary-color-p4;
                color: #fff;
                border-color: $secondary-color-p4;
            }
        }
    }
    &--skyblue {
        border-radius: 0;
        color: #ffffff;
        font-size: 16px;
        font-weight: 500;
        background: $sky-blue;
        &:hover {
            background: rgba($sky-blue, 0.7);
            color: #fff;
        }
    }
    &--blue {
        color: #fff;
        background: $blue;
        font-size: 16px;
        font-weight: 500;
        border-radius: 5px;
        &:hover {
            background: rgba($blue, 0.7);
            color: #fff;
        }
    }
    &--rounded{
        border-radius: 500px;
    }
    &-width{
        &--125 {
            min-width: 125px;
        }
        &--215 {
            min-width: 215px;
        }
        &--md {
            min-width: 160px;
        }
        &--170{
            min-width: 170px;
        }
        &--lg {
            min-width: 193px;
        }
    }

    &-height{
        &--xsm{
            height: 45px;
        }
        &--55{
            height: 55px;
        }
    }
}

.store-btn{
    min-width: 239px;
    // min-height: 75px;
    border-radius: 5px;
    background-color: #0f53fa;
    display: inline-flex;
    align-items: center;
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 5px;
    padding-bottom: 10px;
    color: #fff;
    border: 2px solid #0f53fa;
    i{
        font-size: 34px;
    }
    .text{
        padding-left: 15px;
        .small-text{
            color: #e9f7fe;
            font-size: 16px;
            font-weight: 300;
            line-height: 1;
        }
        h5{
            line-height: 1;
            color: #fff;
        }
    }
    &:hover{
        border-color: #e2e4e8;;
        background-color: #fff;
        .small-text{
           color: #7c8087;
        }
        h5{
            color: #1d293f;
        }
        i{
            color: #0f53fa;
        }
    }
}