
.landing-4{


 /* /////////////////////////// 
    Landing 4 Commons
//////////////////////////// */


.section-content-group {
  .p-title-2 {
      font-size: 48px;
      line-height: 60px;
      font-weight: 700;
      color: #19224c;
      margin-bottom: 28px;
  }
  p {
      font-size: 18px;
      line-height: 26px;
      color: #5d646d;
      font-weight: 300;
  }
  .btn-p4 {
    margin-top: 28px;
  }
}

.section-title{
  text-align: center;
  h2{
    font-size: 48px;
    line-height:60px;
    font-weight: 700;
    color: #19224c;
  }
}

.back-to-top{
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: -40px;
  right: 25px;
  background: $primary-color-p4;
  border-radius:5px;
  color: #fff;
  border:1px solid $primary-color-p4;
  transition: .4s;
  &:hover{
    background: transparent;
    color: $primary-color-p4;
  }
  &.show{
    bottom: 24px;
  }
}
 /* /////////////////////////// 
    Header Area
//////////////////////////// */
  .site-header{
    background: #5473e8;
    @include tablet{
      padding-top: 20px;
    }
    @include desktops{
      padding-top: 15px;
    }
    .main-menu{
      margin: 0 -18px;
    }
    .brand-logo {
      position: relative;
      top: -2px;
    }
  }
  .main-menu{
    @include desktops{
      display: flex;
      justify-content: flex-end;
    }
    >li{
      >a{
        @include desktops{
          color: #fff;
          font-size: 16px;
          font-weight: 300;
          line-height: 24px;
          padding: 18px;

        }
        &:hover{
          color: $secondary-color-p4;
        }
      }
    }
    .has-dropdown{
     @include desktops{
      position: relative;
      z-index: 99;
     }
     &:hover{
      .menu-dropdown{
        @include desktops{
          top: 100%;
          opacity: 1;
          pointer-events: visible;
        }
        p{
          margin-bottom: 0;
        }
      }
     }
    }
    .menu-dropdown{
      @include desktops{
        display: flex;
        top: 110%;
        position: absolute;
        flex-wrap: wrap;
        min-width: 530px;
        box-shadow: 0 12px 24px rgba(0, 0, 0, 0.03);
        border-radius: 8px;
        border: 1px solid #e5e5e5;
        background-color: #ffffff;
        padding: 30px 30px 0px;
        z-index: 99;
        opacity: 0;
        transition: opacity .4s,top .4s;
        pointer-events: none;
        left: -90%;
      }
      .single-item{
        @include desktops{
          flex: calc(50% - 20px);
          max-width: calc(50% - 20px);
          margin-bottom: 20px;
        }
        &:hover{
          a{
           h3{
             
            @include desktops{
              color: $secondary-color-p4;
             }
           }
          }
        }
        a{
            h3{
              color: #1f1f1f;
              font-size: 16px;
              font-weight: 700;
              letter-spacing: -0.4px;
              transition: .4s;
              padding: 0;
              padding-bottom: 5px;
              line-height: 1;
            }
            p{
              margin-bottom: 0;
            }
        }
        p{
          color: #878b90;
          font-size: 14px;
          font-weight: 400;
          letter-spacing: -0.35px;
        }
        &:nth-child(odd){
             @include desktops{
                margin-right: 40px;
             }
        }
      }
    }
  }
  
  .header-btns {
    position: relative;
    display: flex;
    align-items: center;
    @include desktops{
      justify-content: center;
    }
    .btn-link{
      color: #666666;
      font-size: 16px;
      font-weight: 400;
      letter-spacing: -1px;
      &:hover{
        color: $primary-color-p4;
      }
    }
    &.btns-group{
      margin: 0 -17px;
    }
  
    
  }
  
  /*  Mobile Menu / Mean Menu Reset  */ 
  .hamburger-icon {
    font-size: 16px;
    color: #fff;
    width: 36px;
    height: 36px;
    border: 2px solid #fff;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: .4s;
    &:hover {
        background: $secondary-color-p4;
        color: #fff;
        border-color: transparent;
    }
  }
  
  .main-navigation {
  display: flex;
  justify-content: flex-end;
  .header-btns{
    margin-left: 50px;
  }
  }
  .mobile-header {
    padding: 18px 0;
    background: #5473e8;
  }
  
  /* Mobile Off canvas */
  
  .off-canvas-wrapper {
    top: 0;
    right: 0;
    height: 100vh;
    position: fixed;
    background-color: #fff;
    z-index: 9999;
    transition: 0.4s;
    transform: translateX(100%);
    box-shadow: 0 0 87px 0 rgba(0, 0, 0, 0.09);
    opacity: 0;
    visibility: hidden;
    width: 250px;
    @include mobile {
        width: 300px;
    }
  &.open {
      transform: translateX(0);
      opacity: 1;
      visibility: visible;
  }
  .off-canvas-inner {
      padding-top: 30px;
      padding-left: 20px;
      padding-right: 20px;
      height: 100%;
      position: relative;
      overflow-y: scroll;
  }
  .header-btns {
    flex-direction: column;
    align-items: unset;
    .btn {
      &:first-child {
        margin-bottom: 16px;
      }
    }
  }
  .btn-close-off-canvas {
      top: 0;
      left: -50px;
      width: 50px;
      height: 50px;
      font-size: 20px;
      background-color: $secondary-color-p4;
      color: #fff;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
      position: absolute;
      -webkit-transition: 0.4s;
      -o-transition: 0.4s;
      transition: 0.4s;
      display: flex;
      justify-content: center;
      align-items: center;
  }
  
  .menu-expand {
      display: none;
  }
  }
  
  .mobile-navigation {
  padding: 20px 0;
  .mobile-menu {
      >li {
          padding-bottom: 13px;
          margin-bottom: 13px;
          border-bottom: 1px solid #ececec;
          .sub-menu {
              >li {
                  padding-top: 13px;
                  margin-top: 13px;
                  border-top: 1px solid #ececec;
                  padding-left: 20px;
                  &.single-item{
                    h3 {
                        font-size: 15px;
                        margin-bottom: 5px;
                        font-weight: 600;
                        line-height: 1;
                    }
                    p{
                        font-size: 13px;
                    }
                    a{
                        &:hover{
                            color: inherit;
                        }
                    }
                 }
              }
          }
          &:last-child {
            border-bottom-color: transparent;
          }
      }
      li {
        i {
          margin-left: 8px;
          position: relative;
          top: 3px;
        }
        &:hover {
            >a {
                color: $primary-color-p4;
            }
        }
      }
      a {
          display: block;
      }
  }
  }


/* /////////////////////////// 
    Hero Area
//////////////////////////// */
  .hero-area{
    height: 100%;
    width: 100%;
    position: relative;
    z-index: 1;
    &:before{
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      z-index: -1;
      background:url(../image/page-4/hero-area.png);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      z-index: -1;
      @include tablet{
        height: 80%;
      }
    }
    .hero-content{
      color: #fff;
      padding-top: 75px;
      @include mobile-lg{
        padding-top: 125px;
        padding-bottom: 35px;
      }
      .title{
        font-weight: 700;
        color: inherit;
        margin-bottom: 20px;
        font-size: 45px;
        line-height: 55px;
        @include mobile-lg{
          font-size: 60px;
          line-height: 68px;
        }
      }
      p{
        font-size: 18px;
        line-height: 26px;
        font-weight: 300;
        color:#fff;
      }
      .hero-btns{
        padding-top:10px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        @include brk-point(350px){
          flex-direction: row;
        }
        
        a{
          margin-bottom: 10px;
          @include brk-point(350px){
            margin-right:20px;
            margin-bottom: 0px;
          }
          &:last-child{
            margin-right: 0;
          }
        }
        .video-link{
          align-items: center;
          color: #fff;
            display:flex;
            @include brk-point(350px){
              display: inline-flex;
            }
            i{
              font-size: 26px;
              margin-right: 10px;
            }
            .link-txt{
              line-height: 1;
              font-size: 16px;
            }
        }
      }
    }
    .hero-images{
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index:1;
      margin-top: 83px;
      @include mobile-lg{
        margin-top: 115px;
      }
      .abs-img{
        position: absolute;
        &.image{
          &-1{
            top: -9%;
            right: -2%;
            z-index: -1;
          }
          &-2{
            top: 5%;
            right: -12%;
          }
          &-3{
            top: 22%;
            left: -4%;
            z-index: -1;
          }
          &-4{
            bottom: 15%;
            left: -18%;
          }
        }
      }
    }
    
  }

/* /////////////////////////// 
    Brand Area
//////////////////////////// */
  .brand-wrapper{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -32.5px;
    .single-brand{
      margin-right: 32.5px;
      margin-left: 32.5px;
      margin-bottom: 20px;

      &:last-child{
        margin-right: 0px;
      }
    }
  }


/* /////////////////////////// 
    Feature Area
//////////////////////////// */
  
  .features-card{
    text-align: center;
    margin-top: 30px;
    box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.08);
      padding-top:40px;
      padding-bottom:40px;
      padding-left:35px;
      padding-right:35px;
      @include desktops{
        padding-left:15px;
        padding-right:15px;
      }
      @include large-desktops{
        padding-left:35px;
        padding-right:35px;
      }
    &:first-child{
      margin-top: 0px;
    }
    // .card-inner{
    //   box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.08);
    //   padding-top:40px;
    //   padding-bottom:40px;
    //   padding-left:35px;
    //   padding-right:35px;
    // }
    .icon{
      margin-bottom: 25px;
    }
    .title{
      font-size: 20px;
      color: #0f2137;
      line-height: 1;
      margin-bottom: 20px;
    }
    p{
      font-size: 16px;
      line-height: 26px;
      color: #5d646d;
      font-weight: 300;
      &:last-child{
        margin-bottom: 0;
      }
    }
    
  }
  
  
  
/* /////////////////////////// 
    Content Area
//////////////////////////// */

.content-section{
  position: relative;
  .content-area-shape{
    position: absolute;
    top: 20%;
    right: 0;
    opacity: .5;
  }
}
    .content-img-group{
      position:relative;
      z-index: 1;
      .main-img{
        width: 100%;
      }
      &-1{
        .abs-img{
          position: absolute;
        }
        .image{
          &-1{
            bottom: -35px;
            right: -25%;
          }
          &-2{
            left: -38px;
            top: -30px;
            z-index: -1;
          }
        }
      }
      &-2{
        .abs-img{
          position: absolute;
        }
        .image{
          &-1{
            bottom: -35px;
            left: -25%;
          }
          &-2{
            right: -38px;
            top: -30px;
            z-index: -1;
          }
        }
      }
      &-3{
        .abs-img{
          position: absolute;
        }
        .image{
          &-1{
            bottom: -35px;
            left: -25%;
            @include desktops{
              left: auto;
              right: -25%;
            }
            @include brk-point(1320px){
              right: auto;
              left: -25%;
            }
          }
          &-2{
            left: -38px;
            top: -30px;
            z-index: -1;
          }
        }
      }
    }
    
    
  }
  /* /////////////////////////// 
    Testimonial Area
  //////////////////////////// */
.testimonial-slider-p4{
  .slick-list{
   @include tablet{
      margin: 0 -30px;
   }
  }
  .slick-dots{
    display: flex;
    justify-content: center;
    margin-top: 50px;
    li{
      font-size: 0;
      height: 12px;
      width: 12px;
      background: #e7f1ed;
      border-radius: 500px;
      margin: 0 5px;
      &.slick-active{
        background: #5473e8;
      }
    }
  }
  .single-slide{
      margin: 0 15px;
  }
}
.testimonial-card-p4{
  .dialouge-box{
    padding-top: 41px;
    padding-bottom: 41px;
    border: 1px solid #efefef;
    position: relative;
    margin-bottom: 22px;
    padding-left: 20px;
    padding-right: 20px;
    @include mobile-lg{
      padding-left: 43px;
      padding-right: 43px;
    }
    &:before{
      position: absolute;
      content: '';
      left: 46px;
      bottom: 0;
      transform: translateY(30px);
      border-color: white transparent transparent transparent;
      border-width: 15px;
      border-style: solid;
      z-index: 2;
    }
    &:after{
      position: absolute;
      content: '';
      left: 45px;
      bottom: 0;
      transform: translateY(34px);
      width: 10px;
      border-color: #f3f3f3 transparent transparent transparent;
      border-width: 16px;
      border-style: solid;
      z-index: 1;
    }
    .title{
        font-size: 16px;
        line-height: 40px;
        color: #19224c;
    }
    p{
      font-size: 19px;
      line-height: 32px;
      color: #5d646d;
      font-weight: 300;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .user-profile{
    display: flex;
    align-items: center;
    margin-left: 36px;
    .image{
      min-width: 50px;
      max-width: 50px;
      border-radius: 500px;
    }
    .profile-text{
      padding-left: 14px;
      padding-top: 5px;
      .title{
        font-size: 16px;
        font-weight: 700;
        line-height: 1;
        margin-bottom: 4px;
      }
      p{
        font-size: 14px;
        font-weight: 300;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
}

.pricing-section{
  position: relative;
  overflow: hidden;
  .pricing-shape{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    opacity: .4;
  }
}
.pricing-card-p4{
  text-align: center;
  color: #5d646d;
  padding-top: 47px;
  padding-bottom: 50px;
  border-style: solid;
  border-width: 1px;
  border-color: rgb(239, 239, 239);
  border-radius: 5px;
  padding-left: 30px;
  padding-right: 30px;
  background: #fff;
  box-shadow: 0px 25px 49px 0px rgba(0, 0, 0, 0);
  transition: .4s;
  @include mobile-lg{
    padding-left: 40px;
    padding-right: 40px;
  }
  &.active{
      border-style: solid;
      border-width: 1px;
      border-color: rgb(238, 238, 238);
      border-radius: 5px;
      background-color: rgb(255, 255, 255);
  }
  &:hover{
    box-shadow: 0px 25px 49px 0px rgba(0, 0, 0, 0.1);
  }
  .price-head{
    margin-bottom: 12px;
    .title{
      font-size: 22px;
      font-weight: 700;
      color: #0f2137;
      line-height: 1;
      margin-bottom: 13px;
    }
    p{
      font-size: 16px;
      line-height: 28px;
      margin-bottom: 0;
      font-weight: 300;
    }
  }
  .price-amout-block{
    margin-bottom: 40px;
    .price-number{
      // margin-bottom: 10px;
      font-size: 48px;
      font-weight: 500;
    }
    p{
      font-size: 14px;
      color: #5d646d;
      margin-bottom: 0;
    }
  }
  .price-list-block{
      li{
        font-size: 16px;
        color: #5d646d;
        margin-bottom:16px;
        font-weight: 300;
      }
  }
  .price-btn{
    padding-top: 25px;
    .btn-p4{
      min-width: 190px;
    }
  }

}


.cta-section-p4{
  background: #f7f8fe;
}
.cta-content{
  text-align: center;
  .icon{
    margin-bottom: 26px;
  }
  .title{
    font-size: 48px;
    line-height: 55px;
    color:#19224c;
    font-weight: 700;
    margin-bottom: 18px;
  }
  p{
    font-size: 18px;
    line-height: 26px;
    color: #5d646d;
    font-weight: 300;
  }
  .cta-btn{
    padding-top: 10px;
  }
}


.site-footer-p4{
  padding-top: 110px;
  padding-bottom: 90px;
  .footer-title{
      font-size: 16px;
      font-weight: 700;
      color: #343d48;
      margin-bottom: 33px;
      line-height: 1;
  }
  .address-block{
    color: #19224c;
    font-size: 16px;
  }
  .footer-list-block {
    padding-right: 30px;
    margin-bottom: 30px;
    @include tablet{
      padding-right: 0px;
      padding-left: 15px;
      margin-left: auto;
    }
    &:first-child{
      padding-left: 0px;
      margin-left: 0;
    }
  }
}

.social-list-p4{
  display: flex;
  a{
    margin-right: 15px;
    font-size: 14px;
    color: #292929;
    &:hover{
      color:#5473e8 ;
    }
  }
}