// 
.job-details-page{
  .section-title{
    h2{
      color: #1d293f;
      font-size: 48px;
      font-weight: 700;
      letter-spacing: -0.83px;
      line-height: 56px;
    }
    p{
      color: #7c8087;
      font-size: 18px;
      font-weight: 300;
      line-height: 32px;
      padding-top: 15px;
      +p{
        padding-top: 0;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}

.job-details-content{
  padding-top: 65px;
  padding-bottom: 100px;
  .block-title{
    color: #1d293f;
    font-size: 21px;
    font-weight: 700;
    line-height: 34px;
  }
  .skill-list{
    li{
      color: #1d293f;
      list-style: none;
      font-size: 16px;
      font-weight: 300;
      line-height: 20px;
      padding-top: 12.5px;
      padding-bottom: 12.5px;
      &:last-child{
        padding-bottom: 0;
      }
      &:first-child{
        padding-top: 0;
      }
      &:before{
        display: inline-block;
        content: "";
        width: 9px;
        height: 9px;
        background-color: #1d293f;
        border-radius: 500px;
        margin-right: 12px;
      }
    }
  }
  p{
    color: #1d293f;
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;
    margin-bottom: 15px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  .job-top-header{
    max-width: 275px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding-bottom: 30px;
    .location{
      color: #8c97ac;
      font-size: 16px;
      font-weight: 300;
      margin-right: 5px;
      display: flex;
      align-items: center;
      margin-right: 10px;
    }
    
    .badge{
      height: 30px;
      border-radius: 5px;
      font-size: 13px;
      font-weight: 300;
      display: flex;
      align-items: center;
      padding-left: 13px;
      padding-right: 13px;
      width: 76px;
      &.green{
        background-color: rgba(15, 188, 73, 0.1);
        color: #0fbc49;
      }
      &.orange{
        background-color:rgba(245, 166, 35, 0.15);;
        color: #f5a623;
      }
      &.blue{
        background-color: rgba(16, 66, 253, 0.15);
        color: #1042fd;
      }
    }
  }
  .contant-block-1{
    .main-title{
      color: #1d293f;
      font-family: "Circular Std Bold";
      font-size: 58px;
      font-weight: 700;
      letter-spacing: -1px;
      line-height: 64px;
      margin-bottom: 75px;
    }
  }
  .contant-block-2{
    .block-title{

    }
   
  }
}