.card-job{
  min-height: 350px;
  box-shadow: 0 2px 4px rgba(14, 86, 124, 0.17);
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 35px;
  transition: .4s;
  .location{
    color: #8c97ac;
    font-size: 16px;
    font-weight: 300;
    margin-right: 5px;
    display: flex;
    align-items: center;
  }
  
  .card-footer{
    background: transparent;
    border: 0;
    padding: 0;
    display: flex;
    align-items: center;
    .client-details{
      display: flex;
      flex-direction: column;
  
      .company-title{
        color: #1d293f;
        font-size: 18px;
        font-weight: 500;
        line-height: 1;
      }
      span{
        line-height: 1;
        color: #8c97ac;
        font-size: 13px;
        font-weight: 300;
      }
    }
  }
  .badge{
    height: 30px;
    border-radius: 5px;
    font-size: 13px;
    font-weight: 300;
    display: flex;
    align-items: center;
    padding-left: 13px;
    padding-right: 13px;
    &.green{
      background-color: rgba(15, 188, 73, 0.1);
      color: #0fbc49;
    }
    &.orange{
      background-color:rgba(245, 166, 35, 0.15);;
      color: #f5a623;
    }
    &.blue{
      background-color: rgba(16, 66, 253, 0.15);
      color: #1042fd;
    }
  }
  &:hover {
    box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
  }
  &.top-only{
    justify-content: flex-start;
    min-height: 100%;
  }
}

.testimonial-card{
  padding: 30px 25px;
  border:1px solid #e2e4e8;
  border-radius: 10px;
  width: 100%;
  @include tablet{
    max-width: 350px;
    min-width: 350px;
  }
  .card-img{
    width: 48px;
    height: 48px;
    border-radius:500px ;
    overflow: hidden;
  }
  .card-text{
    p{
      color: #1d293f;
    }
  }
}

// Page 2 (Plugin Page)
.explore-card {
  padding: 70px 36px 40px;
  box-shadow: 0 2px 4px rgba(14, 86, 124, 0.17);
  background-color: #ffffff;
  height: 222px;
  min-width: 250px;
  transition: .4s;
  width: 100%;
  cursor: grab;
  .excerpt {
    color: $text-light;
    margin-bottom: 0;
  }
  &:hover {
    box-shadow: 0 32px 84px rgba(14, 86, 124, 0.17);
  }
}

// Page 3 (Desktops Page)

.solution-card{
  background: #f7f8fa;
  display: flex;
  padding: 25px 20px 0;
  border-radius: 10px;
  max-width: 350px;
  .card-icon{
    min-height:55px;
    min-width:55px;
    max-height:55px;
    max-width:55px;
    border-radius: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 19px;
    background: #fff;
    color: $primary-color-p3;
    i{
      color: inherit;
    }
  }
  .card-text{
    padding-left: 20px;
    .title{
      color: #1d293f;
      font-size: 21px;
      font-weight: 700;
      line-height: 34px;
      margin-bottom: 4px;
    }
    p{
      color: #7c8087;
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
    }
  }
}


.social-card{
  border-radius: 10px;
  border: 1px solid #e2e4e8;
  background-color: #ffffff;
  padding: 20px 18px;
  .author-card{
    padding: 0;
    margin-bottom: 15px;
    .corner-icon{
      position: absolute;
      right: 0;
      top: 0;
    }
  }
  p{
    &:last-child{
      margin-bottom: 0;
    }
  }
}
.author-card{
  padding: 20px 18px;
  display: flex;
  align-items: center;
  position: relative;
  .card-img{
    min-width: 41px;
    max-width: 41px;
    max-height: 41px;
    min-height: 41px;
    overflow: hidden;
    border-radius: 500px;
    margin-right:14px;
  }
  .title{
    line-height: 1;
  }
  .subtitle{
    line-height: 1;
  }
  .corner-icon{
    position: absolute;
    right: 18px;
      top: 20px;
     color: #21c6dd;
     i {
       color: #21c6dd;
     }
  }
}

