// 
.about-page{
  .section-title{
    h2{
      color: #1d293f;
      font-size: 48px;
      font-weight: 700;
      letter-spacing: -0.83px;
      line-height: 56px;
    }
    p{
      color: #7c8087;
      font-size: 18px;
      font-weight: 300;
      line-height: 32px;
      padding-top: 15px;
      +p{
        padding-top: 0;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  
  .content-img-group{
    position:relative;
    z-index: 1;
    .main-img{
      width: 100%;
    }
    &-1{
      .abs-img{
        position: absolute;
      }
      .image{
        &-1{
          bottom: -35px;
          right: -25%;
        }
        &-2{
          left: -38px;
          top: -30px;
          z-index: -1;
        }
      }
    }
  }
.copyright-bar{
  border-top:1px solid #313d55;
  padding-top: 30px;
  padding-bottom: 30px;
}
.social-links{
  margin: 0 -10px;
  a{
    margin:0 10px;
    color: #fff;
  }
}
}



/*---------------------
  About Content Section
-----------------------*/
.about-content-section{
  padding-bottom: 145px;
}
.about-content-text{
   p{
    color: #1d293f;
    font-size: 18px;
    font-weight: 300;
    line-height: 32px;
    margin-bottom: 35px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  
}
.about-content-text-wrapper{
  .user-review-block{
    p{
      color: #7c8087;
      font-size: 13px;
      font-weight: 300;
      line-height: 20px;
    }
    .user-name{
      color: #1d293f;
      font-size: 13px;
      font-weight: 500;
      line-height: 1;
    }
  }
}

/*---------------------
  About Team Section
-----------------------*/
.team-card{
  display: flex;
  align-items: center;
  border-radius: 8px;
  border: 1px solid #e2e4e8;
  background-color: #ffffff;
  padding: 30px 20px;
  .card-img{
    margin-right: 20px;
    min-width: 88px;
    max-width: 88px;
    min-height: 88px;
    border-radius: 500px;
    overflow: hidden;
  }
  .card-content{
    .title{
      color: #1d293f;
      font-size: 21px;
      font-weight: 700;
      line-height: 34px;
    }
    span{
      color: #8c97ac;
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
      line-height: 1;
    }
  }
}

/*---------------------
  About CTA Section
-----------------------*/
.about-cta{
  padding-top: 100px;
  padding-bottom: 110px;
}
.about-cta-content{
  .title{
    color: #1d293f;
    font-size: 34px;
    font-weight: 700;
    letter-spacing: -0.59px;
    line-height: 45px;
    margin-bottom: 35px;
  }
}

/*---------------------
  About Fun Section
-----------------------*/
.about-fun-section{
  padding-top: 100px;
  padding-bottom: 100px;
}
.single-fun-fact{
  .number{
    height: 70px;
    color: #1d293f;
    font-size: 70px;
    font-weight: 700;
    letter-spacing: -1px;
    line-height: 1;
    margin-bottom: 24px;
  }
  .text{
    color: #1d293f;
    font-size: 16px;
    font-weight: 300;
    line-height: 21px;
  }
}


.card-height1 {
  height: 465px;
}
.card-height2 {
  height: 228px;
}
.card-height2 {
  height: 225px;
}

.sin-block {
  display: flex;
  flex-wrap: wrap;
  &.big-block1 {
    flex-direction: column;
    flex-shrink: 0;
  }
  &.sm-block {
    flex-shrink: 0;
  }
}

.macy-container {
  display: flex;
}

.macy-inner-wrap {
    display: flex;
}
.single-macy {
  margin-bottom: 16px;
  margin-left: 8px;
  margin-right: 8px;
}