.desktop-page{

  /*Common Classes*/
  
  .section-title{
    text-align: center;
    h2{
      color: #1d293f;
      font-size: 48px;
      font-weight: 700;
      letter-spacing: -0.83px;
      line-height: 56px;
    }
    p{
      color: #7c8087;
      font-size: 18px;
      font-weight: 300;
      line-height: 32px;
    }
  }

  /* /////////////////////////// 
    Header Area
//////////////////////////// */
.site-header{
    @include tablet{
      padding-top: 20px;
    }
    @include desktops{
      padding-top: 15px;
    }
    .main-menu{
      margin: 0 -18px;
    }
    .brand-logo {
      position: relative;
      top: -2px;
    }
  }
  .main-menu{
    @include desktops{
      display: flex;
      justify-content: flex-end;
    }
    >li{
      >a{
        @include desktops{
          color: #000000;
          

          font-size: 16px;
          font-weight: 400;
          line-height: 24px;
          padding: 18px;
        }
        &:hover{
          color: $primary-color;
        }
      }
    }
    .has-dropdown{
     @include desktops{
      position: relative;
      z-index: 99;
     }
     &:hover{
      .menu-dropdown{
        @include desktops{
          top: 100%;
          opacity: 1;
          pointer-events: visible;
        }
        p{
          margin-bottom: 0;
        }
      }
     }
    }
    .menu-dropdown{
      @include desktops{
        display: flex;
        top: 110%;
        position: absolute;
        flex-wrap: wrap;
        min-width: 530px;
        box-shadow: 0 12px 24px rgba(0, 0, 0, 0.03);
        border-radius: 8px;
        border: 1px solid #e5e5e5;
        background-color: #ffffff;
        padding: 30px 30px 0px;
        z-index: 99;
        opacity: 0;
        transition: opacity .4s,top .4s;
        pointer-events: none;
        left: -90%;
      }
      .single-item{
        @include desktops{
          flex: calc(50% - 20px);
          max-width: calc(50% - 20px);
          margin-bottom: 20px;
        }
        &:hover{
          a{
           h3{
            @include desktops{
              color: $primary-color;
             }
           }
          }
        }
        a{
            h3{
              color: #1f1f1f;
              font-size: 16px;
              font-weight: 700;
              letter-spacing: -0.4px;
              transition: .4s;
              padding: 0;
              padding-bottom: 5px;
            }
            p{
              margin-bottom: 0;
            }
        }
        p{
          color: #878b90;
          font-size: 14px;
          font-weight: 400;
          letter-spacing: -0.35px;
        }
        &:nth-child(odd){
             @include desktops{
                margin-right: 40px;
             }
        }
      }
    }
  }
  
  .header-btns {
    position: relative;
    display: flex;
    align-items: center;
    @include desktops{
      justify-content: center;
    }
    .btn-link{
      color: #666666;
      font-size: 16px;
      font-weight: 400;
      letter-spacing: -1px;
      &:hover{
        color: $primary-color;
      }
    }
    &.btns-group{
      margin: 0 -17px;
    }
  
    
  }
  
/*  Mobile Menu / Mean Menu Reset  */ 
.hamburger-icon {
  font-size: 16px;
  color: $primary-color;
  width: 36px;
  height: 36px;
  border: 2px solid $primary-color;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: .4s;
  &:hover {
      background: $primary-color;
      color: #fff;
      border-color: transparent;
  }
}

.main-navigation {
  display: flex;
  justify-content: flex-end;
  .header-btns{
    margin-left: 50px;
  }
}
.mobile-header {
  padding: 18px 0;
}

/* Mobile Off canvas */

.off-canvas-wrapper {
  top: 0;
  right: 0;
  height: 100vh;
  position: fixed;
  background-color: #fff;
  z-index: 9999;
  transition: 0.4s;
  transform: translateX(100%);
  box-shadow: 0 0 87px 0 rgba(0, 0, 0, 0.09);
  opacity: 0;
  visibility: hidden;
  width: 250px;
  @include mobile {
      width: 300px;
  }
  &.open {
      transform: translateX(0);
      opacity: 1;
      visibility: visible;
  }
  .off-canvas-inner {
      padding-top: 30px;
      padding-left: 20px;
      padding-right: 20px;
      height: 100%;
      position: relative;
      overflow-y: scroll;
  }
  .header-btns {
    flex-direction: column;
    align-items: unset;
    .btn {
      &:first-child {
        margin-bottom: 16px;
      }
    }
  }
  .btn-close-off-canvas {
      top: 0;
      left: -50px;
      width: 50px;
      height: 50px;
      font-size: 20px;
      background-color: $primary-color;
      color: #fff;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
      position: absolute;
      -webkit-transition: 0.4s;
      -o-transition: 0.4s;
      transition: 0.4s;
  }

  .menu-expand {
      display: none;
  }
}

.mobile-navigation {
  padding: 20px 0;
  .mobile-menu {
      >li {
          padding-bottom: 13px;
          margin-bottom: 13px;
          border-bottom: 1px solid #ececec;
          .sub-menu {
              >li {
                  padding-top: 13px;
                  margin-top: 13px;
                  border-top: 1px solid #ececec;
                  padding-left: 20px;
                  &.single-item{
                    h3 {
                        font-size: 15px;
                        margin-bottom: 5px;
                        font-weight: 600;
                    }
                    p{
                        font-size: 13px;
                    }
                    a{
                        &:hover{
                            color: inherit;
                        }
                    }
                 }
              }
          }
          &:last-child {
            border-bottom-color: transparent;
          }
      }
      li {
        i {
          margin-left: 8px;
          position: relative;
          top: 3px;
        }
        &:hover {
            >a {
                color: $primary-color;
            }
        }
      }
      a {
          display: block;
      }
  }
}





/* Hero area  */
.hero-image-group{
  position: relative;
  z-index: 1;

  .shape{
    position: absolute;
    z-index: -1;
    &-1{
      right: 0;
      top: 0;
    }
    &-2{
      left: 0;
      bottom: 0;
      margin-left:-30px;
      margin-bottom:-30px;
    }
  }
}


.brand-images {
  display: flex;
  align-items: center;
  margin: -10px -35px;
  margin-right: -40px;
  flex-wrap: wrap;
  justify-content: center;
  // @include tablet{
  //   justify-content: space-between;
  // }
  .single-brand {
    margin: 10px 0;
    opacity: .6;
    margin-right: 40px;
    margin-left: 35px;
  }
}



  /* Solution Area */ 

  .solution-area{
    padding-bottom: 120px;
    border-bottom:1px solid #eceff4;
  }
  .solution-slider{
    .slick-list{
      margin: 0 -15px;
    }
    .single-slide{
      margin: 0 15px;
    }
  }
  .solution-area-btn{
    .btn-p3{
      i{
        font-size: 19px;
        margin-right: 8px;
      }
    }
  }

  .list-with-icon{
    li{
      display: flex;
      align-items: center;
      margin-bottom: 18px;
      i{
        min-width: 59px;
        min-height: 59px;
        max-width: 59px;
        max-height: 59px;
        background-color: #f7f8fa;
        color: $primary-color-p3;
        border-radius: 500px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        margin-right: 15px;
      }
      .text{
        margin-bottom: 0;
        color: #1d293f;
        font-size: 24px;
        font-weight: 500;
        line-height: 34px;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
    .list-badge{
      width: 42px;
      height: 25px;
      border-radius: 5px;
      background-color: #ffd166;
      color: #1d293f;
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 20px;
    }
    
  }


  /* Content Image 1 */
.content-section {
    position: relative;
    padding-top: 155px;
    padding-bottom: 155px;
}

.content-right-side-image {
    @include desktops{
      right: -6%;
      width: 50%;
      height: 100%;
      top: 10%;
      position: absolute;
    }
    @include large-desktops{
      right: 0;
    }
    @include brk-point(1600px){
      right: -16%;
    }
    .image {
      position: absolute;
      top: 0;
      left: 0px;
      &-1 {
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);
        margin-top: -40px;
        z-index: 1;
      }
      &-2 {
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);
        margin-top: -40px;
      }
      &-3 {
        z-index: -1;
        // top: 50%;
        // transform: translateY(-50%);
        margin-top: -40px;
        left: -13%;
      }
      img {
        max-width: unset;
      }
    }    
}
.content-img-group-2{
  position: relative;
  .absolute-image {
    position: absolute;
    top: -45px;
    left: 0;
    transform: translate(45px);
  }
}

.content-btns{
  a{
    margin-top: 14px;
  }
}




/*----- Testimonial Shape -----*/ 
.testimonial-section{
  padding-top: 150px;
  padding-bottom:150px;
}
.shape-holder{
  position: absolute;
  &.testimonial-shape{
    top:0;
    right: 0;
  }
}


/*----- Pricing Area -----*/ 
.pricing-area{
  padding-top:80px;
  padding-bottom:90px;
  @include desktops{
    padding-top:140px;
    padding-bottom:150px;
  }
}
.pricing-head{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .single-head{
    &.buttons{
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      @include tablet{
        width: auto;
      }
    }
  }
}
.pricing-block{
  // display: flex;
  // flex-direction: column;
  // height: auto;
  padding-top: 35.5px;
  padding-bottom: 28.5px;
  border-top: 1px solid #eceff4;
  
  .pricing-amount-block{
    h1{
      color: #1d293f;
      font-size: 58px;
      font-weight: 500;
      letter-spacing: -1px;
      line-height: 64px;
      span{
        font-size: inherit;
      }
    }
    p{
      color: #7c8087;
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
    }
  }
  .pricing-list{
    li{
      font-size: 18px;
      color: #1d293f;
      font-weight: 300;
      line-height: 40px;
    }
  }

  .pricing-amount-block{
    .monthly{
      display: none;
    }
    
  }
  &.change-value{
    .pricing-amount-block{
      .monthly{
        display: block;
      }
      .yearly{
        display: none;
      }
    }
  }
}

.pricing-option-buttons{
  width: 98%;
  border-radius: 10px;
  border: 2px solid #ffd166;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  @include mobile{
    width: 344px;
  }
  a{
    width: 50%;
    color: #1d293f;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    height: 55px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    &.active{
      background: #ffd166;
    }
  }
}


.shape-holder{
  position: relative;
  z-index: -1;
}
.footer-shape{
  position: absolute;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  z-index: -1;
  .shape{
    position: absolute;
    z-index: 1;
    &-top{
      top:0;
      right: 0;
      transform: translate(65%,-50%);
    }
    &-bottom{
      bottom:0;
      left: 0;
      transform: translate(-50%, 50%);
    }
  }
}

.single-footer-wrapper{
  .single-footer{
    
  }
}
.copyright-bar{
  border-top:1px solid #313d55;
  padding-top: 30px;
  padding-bottom: 30px;
}
.social-links{
  margin: 0 -10px;
  a{
    margin:0 10px;
    color: #fff;
  }
}
}

