
.desktop2k-page {
    .site-nav {
        justify-content: flex-start;
        .btn {
            min-width: 142px;
            height: 45px;
            border-radius: 8px;
            color: #ffffff;
            font-family: "CircularStd", sans-serif;
            font-size: 16px;
            font-weight: 500;
            letter-spacing: -0.4px;
            &--blue {
                background-color: #258aff;
            }
        } 
    }
    
    header.branding-left {
        .site-nav{
        //   justify-content: space-between;
          padding-top: 28px;
          padding-bottom: 20px;
          .navbar-brand{
            @include desktops{
              flex: 0 0 auto;
            }
          }
          .navbar-nav{
            @include desktops{
              flex-basis: 60%;
              padding-left: 22px;
              border-left: 1px solid #d5d5d5;
              margin-left: 18px;
            }
            .nav-item{
              .nav-link {
                font-size: 16px;
                font-weight: 300;
                margin-right: 30px;
                color: #505050;
                font-family: "CircularStd", sans-serif;
                &:hover, &:active {
                    color: #258aff;
                }

              }
            }
          }
        }
      }

    .hero-content {
        h1 {
            color: #000000;
            font-family: "CircularStd", sans-serif;
            font-weight: 700;
            letter-spacing: -1.73px;
            font-size: 42px;
            line-height: 56px;
            @include tablet {
              font-size: 58px;
              line-height: 62px;
            }
            @include desktops {
              font-size: 62px;
              line-height: 72px;
            }
            @include large-desktops {
                font-size: 70px;
                line-height: 76px;
            }       
        }
        p {
            color: #606060;
            letter-spacing: -0.52px;
        }
        .icon-border {
            width: 22px;
            height: 18px;
            border: 2px solid #e7e7e7;
            text-align: center;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            color: #258aff;
            margin-right: 10px;
        }
    }
    .hero-btn {
        .btn {
            color: #ffffff;
            font-family: "CircularStd", sans-serif;
            font-size: 21px;
            font-weight: 500;
            letter-spacing: -0.52px;
            width: 229px;
            height: 75px;
            box-shadow: 0 32px 64px rgba(37, 138, 255, 0.19);
            border-radius: 8px;
            background-color: #258aff;
        }
    }
}






